h1,
h2,
h3,
h4,
h5,
th {
  font-family: "Acme", sans-serif;
}

span,
p,
li,
tr {
  font-family: "Ubuntu", sans-serif;
}

ul {
  list-style: none;
  padding: 0;
  margin-top: 1rem;
}
.app {
  background: #bb93a9;
  padding: 30px;
  height: 100vh;
}

.logo {
  width: 40px;
  height: 40px;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 8vh;
  background: white;
  color: rgb(24, 22, 22);
}

.nav-links {
  width: 70%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.nav-links a {
  text-decoration: none;
}

a {
  text-decoration: none !important;
}

.nav-links a:hover {
  background-color: palevioletred;
  border-radius: 5px;
}

.homepage-title {
  text-align: left;
  font-size: 40px;
  max-width: 100%;
}

/* container */

.gallery-container {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  flex-flow: row wrap;
  margin: 3em;
  background-color: rgba(193, 195, 199, 0.5);
}

.card-img-top {
  height: 250px;
}

.card {
  /* border: 2px solid rgb(206, 195, 195); */
  box-shadow: 0px 0px 0px 1px rgb(219, 192, 200);
  transition: box-shadow 0.6s linear;
  margin: 0.5em;
}

.card:hover {
  box-shadow: 5px 8px rgb(221, 143, 166);
}

.card-deck {
  margin-top: 5em;
}
.card-deck .card {
  margin-bottom: 4em;
}

/* banner sizing */

.banner-sizing {
  height: 200px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  color: white;
  margin-bottom: 56px;
}

.banner-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 40px;
}

.button_primary-color {
  background-color: rgb(183, 164, 207) !important;
  border: none !important;
}

.pet-button {
  margin-top: 10px !important;
  margin-right: 0.75em !important;
  display: flex;
}

.remove-button {
  margin-left: 10px;
}
.fav_button {
  background-color: white !important;
  padding: 0 !important;
}

.homepage-result {
  margin-top: 2em;
}

#block {
  width: 100%;
  height: 700px;
}

#list {
  width: 100%;
  display: grid;
  grid-gap: 50px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

#list .card {
  max-width: 250px;
}

@media (min-width: 576px) {
  .card-deck {
    margin-right: -8em;
    margin-left: 0;
  }
  .card-deck .card {
    margin-right: 0;
    margin-left: 0;
  }
}

.card-body {
  flex: 1 1 auto;
  min-height: 1px;
  padding: 1.25rem;
  justify-content: space-between;
  align-items: flex-end;
  flex-flow: row wrap;
}

.product_card-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.basket_list-padding {
  padding-bottom: 1em;
  display: flex;
  justify-content: space-between;
}

/* cart page checkout */

.cart__page-left-title {
  font-size: 12px;
  line-height: 19.416px;
  line-height: 13px;
  color: #808284;
  margin-bottom: 20px;
  display: block;
  cursor: pointer;
  background-color: white;
  border: none;
}

.cart__page-left-title:hover {
  background-color: white;
  color: #808284;
}

.cart__page-left-title:active {
  background-color: white;
  color: #808284;
}

.cart__line-item {
  padding: 15px 0;
  position: relative;
  border-bottom: 1px solid #dfe0e1;
  display: flex;
  flex-direction: column;
}

.cart__cart-list {
  margin-bottom: 15px;
  overflow-y: auto;
  border-top: 1px solid #dfe0e1;
  background-color: #fff;
}

.cart-item-content-container {
  display: flex;
}

.cart__product-button {
  background: none;
  border: 0;
  display: block;
  margin: 0;
  padding: 0;
}

.cart__line-item-x-button {
  margin-left: auto;
}

.cart__product-image-container {
  padding: 1em 3em 1em 1em;
}

.cart__line-item-info-title {
  font-size: 13px;
  line-height: 21.034px;
  color: #4c4c4b;
}

.cart__line-item-info-container {
  width: 100%;
  padding-top: 2em;
}

.cart__img {
  width: 150px;
  height: 150px;
  border-radius: 8px;
}

.cart__line-item-info-heading {
  display: flex;
  justify-content: space-between;
}

.overall-page_height {
  min-height: 700px;
  margin-top: 50px;
}

/* cart page summary */

.order_summary_container {
  width: 250px;
  margin: 3em 0 2em 1em;
  display: flex;
  flex-flow: row wrap;
}

#order-details {
  width: 300px;
  margin-bottom: 20px;
  height: auto;
  display: block;
  background-color: #f7f7f8;
  border: 1px solid #f7f7f8;
  position: relative;
}

#order-details .order-summary {
  width: 100%;
}

#order-details .order-summary th {
  font-size: 16px;
  line-height: 25.888px;
  color: #808284;
  letter-spacing: 0;
  text-align: center;
  font-weight: 400;
  vertical-align: middle;
  padding: 15px;
}

#order-details .order-summary__total {
  font-weight: 400;
}

.order-summary__total {
  font-weight: 600;
}

select {
  border: solid rgb(34, 36, 38, 0.15) 1px;
  border-radius: 5px;
  padding: 5px;
}

.checkout__order-button {
  display: block;
  width: 250px;
}

/* confirmation page */

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
}

.confirmation-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
}

.confirmation-icon-container {
  margin: 20px 0;
}

/*about page */
.about-image {
  height: 100vh;
  width: 70vh;
}
.about-headline {
  padding: 1em 0;

}

.about-content p {
  line-height: 2.25em;
    font-size: 1.1rem;
    font-weight: 600;
    letter-spacing: .2px;
    margin-top: 2em;
    margin-bottom: 0;
}

/* mobile responsive */

@media only screen and (max-width: 600px) {
  .nav-links li > span {
    display: none;
  }

  nav > ul {
    padding: 10px 0 0 0;
  }

  h1 {
    font-size: 1.8rem;
  }
  .banner-content {
    padding: 25px;
    text-align: center;
  }
  .banner-content p {
    font-size: 0.8rem;
    padding-top: 10px;
  }
  .banner-sizing {
    margin-bottom: 25px;
  }
  .homepage-title {
    font-size: 1.8rem;
    padding-top: 15px;
    text-align: center;
  }
  .ui.input > input {
    min-width: 250px;
  }

  .card-img-top {
    height: 200px;
  }
  #list .card {
    width: 200px;
  }

  #list {
    grid-gap: 0px;
  }
  .overall-page_height .col-7,
  .overall-page_height .col-9 {
    max-width: 100%;
  }
  .overall-page_height .col-4 {
    max-width: none;
  }
  .cart__line-item-info-heading {
    display: inherit;
  }

  #order-details {
    width: 250px;
  }
  .overall-page_height {
    margin-top: 0;
  }
  .overall-page_height--about_page {
    flex-flow: column wrap;
  }
  h3 {
    font-size: 1rem;
  }
  .about-image {
    height: auto;
    width: 330px;
  }
}
